module.exports = "/* ---------------------------------------------------\r\n    SIDEBAR STYLE\r\n----------------------------------------------------- */\r\n\r\n.wrapper {\r\n    display: flex;\r\n    width: 100%;\r\n    align-items: stretch;\r\n  }\r\n\r\n#sidebar {\r\n    min-width: 250px;\r\n    max-width: 250px;\r\n    /* background: #7386D5; */\r\n    background-image: linear-gradient(\r\n        to bottom,\r\n        rgba(66, 135, 245, 0.52),\r\n        rgba(66, 135, 245, 0.73)\r\n      ),\r\n      url(\"https://wallpapershome.com/images/pages/pic_v/726.jpg\");\r\n    color: #fff;\r\n    transition: all 0.3s;\r\n    box-shadow: 7px 0px 8px -3px rgba(0,0,0,0.62);\r\n  }\r\n\r\n#sidebar.active {\r\n    margin-left: -250px;\r\n  }\r\n\r\n#sidebar .sidebar-header {\r\n    padding: 8px;\r\n    border-bottom: 1px solid #47748b;\r\n  }\r\n\r\n#sidebar ul.components {\r\n    padding: 20px;\r\n    border-bottom: 1px solid #47748b;\r\n  }\r\n\r\n/* TEXTO PRECIO A SECCIONES */\r\n\r\n#sidebar ul p {\r\n    color: #fff;\r\n    padding: 10px;\r\n  }\r\n\r\n#sidebar ul li a {\r\n    padding: 10px;\r\n    font-size: 1rem;\r\n    display: block;\r\n  }\r\n\r\n/* COLOR AL PASAR EL MOUSE HOVER */\r\n\r\n#sidebar ul li a:hover {\r\n    background: rgba(255,255,255,0.20);\r\n    /* color: black; COLOR PARA LAS LETRAS */\r\n    text-decoration: none;\r\n    border-radius: 10px;\r\n    transition: 0.5s;\r\n  }\r\n\r\n#sidebar ul li.active>a,\r\n  a[aria-expanded=\"true\"] {\r\n    /* AQUI COLOR PARA EL ACTIVO QUE NO SE TE OLVIDE OEZIIIII */\r\n    background: rgba(0,0,0,0.20);\r\n    border-radius: 10px;\r\n  }\r\n\r\n#sidebar ul li.active>a:hover{\r\n    color: white;\r\n  }\r\n\r\na[data-toggle=\"collapse\"] {\r\n    position: relative;\r\n  }\r\n\r\n.dropdown-toggle::after {\r\n    display: block;\r\n    position: absolute;\r\n    top: 50%;\r\n    right: 20px;\r\n    -webkit-transform: translateY(-50%);\r\n            transform: translateY(-50%);\r\n  }\r\n\r\n/* SELCCIONES DE LOS DROPDOWN */\r\n\r\nul ul a {\r\n    font-size: 0.9em !important;\r\n    padding-left: 30px !important;\r\n  }\r\n\r\nul.CTAs {\r\n    padding: 20px;\r\n  }\r\n\r\nul.CTAs a {\r\n    text-align: center;\r\n    font-size: 0.9em !important;\r\n    display: block;\r\n    border-radius: 5px;\r\n    margin-bottom: 5px;\r\n  }\r\n\r\na.download {\r\n    background: #fff;\r\n    color: #7386D5;\r\n  }\r\n\r\na.article,\r\n  a.article:hover {\r\n    background: #6d7fcc !important;\r\n    color: #fff !important;\r\n    text-decoration: none;\r\n  }\r\n\r\na{\r\n    color: white;\r\n  }\r\n\r\n/* ---------------------------------------------------\r\n    CONTENT STYLE\r\n  ----------------------------------------------------- */\r\n\r\n#content {\r\n    width: 100%;\r\n    /* padding: 20px; */\r\n    min-height: 100vh;\r\n    transition: all 0.3s;\r\n  }\r\n\r\n/* ---------------------------------------------------\r\n    MEDIAQUERIES\r\n  ----------------------------------------------------- */\r\n\r\n@media (max-width: 768px) {\r\n    #sidebar {\r\n        margin-left: -250px;\r\n    }\r\n    #sidebar.active {\r\n        margin-left: 0;\r\n    }\r\n    #sidebarCollapse span {\r\n        display: none;\r\n    }\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"